/**
* @author shining
* @description 交班主界面
* @date 2017-05-25
**/

import React, { Component } from 'react';
import {message} from 'antd';
import { inject, observer } from 'mobx-react'; 
import Prompt from 'components/prompt-common';//错误提示
import MyScroll from 'react-custom-scrollbars';
import classnames from 'classnames'; 

import './modal.css';
import './shift.less';

message.config({
	top: 300 
});

@inject('shiftStore') @observer
class ShiftRecords extends Component {

	constructor(props, context) { 
        super(props, context);   
        let shiftStore=this.props.shiftStore;   
		shiftStore.setthisclick(1);   
		shiftStore.getConnectData();
		shiftStore.isinWorking();

    }  

	componentDidUpdate() {
        let shiftStore=this.props.shiftStore;  
        let feedback = shiftStore.feedback; 
        if(feedback) {
        //提示
            switch(feedback.status) {
                case 'success':
                    message.success(feedback.msg,shiftStore.closeFeedback());
                    break;
                case 'warn':
                    message.warn(feedback.msg,shiftStore.closeFeedback());
                    break;
                case 'error':
                    message.warn(feedback.msg,shiftStore.closeFeedback());
                    break;
                default:
                    message.info(feedback.msg,shiftStore.closeFeedback());
            }
        }    
    }

	//确认交班
	confirmshift=()=>{  
		let shiftStore=this.props.shiftStore;   
		shiftStore.getsaveWorking();  
	}

	preprint=()=>{
		let shiftStore=this.props.shiftStore;   
		shiftStore.getpreprint(shiftStore.sumworkingID);  
	}

	render() {
		let shiftStore=this.props.shiftStore; 
		let connectdata=shiftStore.connectdata; 
		let workingMoney=shiftStore.workingMoney;
		
		let orderMoneylist=shiftStore.orderMoneylist; 
		let unSubscribeMoneylist=shiftStore.unSubscribeMoneylist;
		let memberMoneylist=shiftStore.memberMoneylist;
		let bookingMoneylist=shiftStore.bookingMoneylist; 
		let feedback = shiftStore.feedback;
		let operatePrompt;
		if(feedback && feedback.status === 'error') {
			//错误提示
			feedback.okClick = () =>{
			if(feedback.confirmClick) feedback.confirmClick();
			feedback.cancelClick();
			}
			feedback.cancelClick = () =>{
			shiftStore.closeFeedback();
			}
			operatePrompt = <Prompt message={feedback} />
		}
		return <div id="shift-main">
				<div className="shift-title">
					<span> {connectdata.createTime} 至 {connectdata.endTime}   </span>
					<span><em>订单餐次:</em> {connectdata.mealsName}</span>
					<span><em>交班人:</em>{connectdata.creatorName}</span>
					<span><em>收银机:</em>{connectdata.deviceCode}</span>
				</div>
				<div className="shift-data">
					<MyScroll>
					 <div className="each-main">
						<div className="each-list">
							<p>
								<span className="each-title">
									<em> 营业统计</em>
								</span> 
							</p>
							<p>
								<span>订单数：{workingMoney.orderNum}</span>
								<span>减免金额：{workingMoney.zhijianAmount}</span>
								<span>收款金额：{workingMoney.yingshouAmount}</span> 
							</p>
							<p>
								<span>人数：{workingMoney.peopleNum}</span>
								<span>折扣金额：{workingMoney.zhekouAmount}</span>
								<span>应收：{workingMoney.yingshouAmount}</span> 
							</p>
							<p>
								<span>消费金额：{workingMoney.xiaofeiAmount}</span>
								<span>服务费：{workingMoney.fuwuAmount}</span>
								<span>开票金额：{workingMoney.kaipiaoAmount}</span> 
							</p>
							<p>
								<span>赠送金额：{workingMoney.zengsongAmount}</span>
								<span>抹零：{workingMoney.molingAmount}</span> 
							</p>  
						</div>

						<div className="each-list-child">
							<p>
								<span className="each-title">
									<em> 订单收款</em>
								</span> 
							</p>
							<div className="each-text-main">
							 {(() =>{
								if(orderMoneylist.length) {
									return orderMoneylist.map((orderMoney,i) => { 
									return  <span key={i}>{orderMoney.payMethodName}:{orderMoney.amount}({orderMoney.num})笔</span>
									});
								}
								})()} 
								
							</div> 
						</div> 

						<div className="each-list-child">
							<p>
								<span className="each-title">
									<em> 会员充值</em>
								</span> 
							</p>
							<div className="each-text-main">
							 {(() =>{
								if(memberMoneylist.length) {
									return memberMoneylist.map((member,i) => {   
											let lempnum;
											if(Number(member.num) === 0){  
												lempnum="("+member.num+")笔";
											}else{
												lempnum='';	
											} 
									return  <span key={i}>{member.payMethodName}:{member.amount}{lempnum}</span>
									});
								}
								})()} 
								
							</div> 
						</div> 

						<div className="each-list-child">
							<p>
								<span className="each-title">
									<em> 预收订金</em>
								</span> 
							</p>
							<div className="each-text-main">
							 {(() =>{
								if(bookingMoneylist.length) {
									return bookingMoneylist.map((bookingMoney,i) => { 
									return  <span key={i}>{bookingMoney.payMethodName}:{bookingMoney.amount}({bookingMoney.num})笔</span>
									});
								}
								})()} 
								
							</div> 
						</div>

						<div className="each-list-child">
							<p>
								<span className="each-title">
									<em>退订</em>
								</span> 
							</p>
							<div className="each-text-main">
							 {(() =>{
								if(unSubscribeMoneylist.length) {
									return unSubscribeMoneylist.map((unSubscribeMoney,i) => { 
									return  <span key={i}>{unSubscribeMoney.payMethodName}:{unSubscribeMoney.amount}({unSubscribeMoney.num})笔</span>
									});
								}
								})()} 
								
							</div> 
						</div> 

						 
					</div>
					</MyScroll>
				</div>

				<div className="shift-button">
					<button className={classnames({
								"disabled": shiftStore.setstate === 2
							})}  onClick={ shiftStore.setstate !== 2 && this.confirmshift}>确认交班</button>
					<button onClick={this.preprint}>预打印</button>
				</div>
				{operatePrompt}
			</div>
	}
}

export default ShiftRecords;


